import React, { Component } from 'react';
import '../css/header.css';

class Header extends Component {
  outMenu = (outState) => {
    this.props.outMenu(false);
  }
  render(){
    return (
      <div style={{height:'90vh', display:'flex', flexWrap:'wrap'}} onMouseOver={this.outMenu}>
        <div style={{flexBasis:'100%', display:'flex', justifyContent:'center', alignItems:'flex-end', backgroundColor:'rgb(240, 179, 35)'}}>
          <div>
            <img className="responsive-img" src='../img/ceo.png' alt=""/>
          </div>
        </div>
        <div style={{flexBasis:'100%', display:'flex', justifyContent:'center', backgroundColor:'#6335cc'}}>
          <div style={{width:'95%'}}>
            <h1 className="white-text" style={{fontFamily:'GoldplayBold', fontSize:'6em', margin:'0%'}}>CRECEMOS JUNTOS</h1>
            <h4 className="white-text" style={{fontFamily:'MonteserratMedium', fontSize:'2.40em', margin:'0%'}}>El mejor negocio es invertir en tu negocio</h4>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;
