import React, { Component } from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCheckCircle} from '@fortawesome/free-solid-svg-icons';

import hoja from '../svg/page-1.svg';

const pagina          = {display:'flex'};
const colSocial       = {margin:'20px', width:'20%'};
const colDistribution = {display:'flex', alignItems:'center', justifyContent:'center'};
const colTexto        = {width:'80%', color:'rgb(240, 179, 35)', fontFamily:'GoldplayMedium', backgroundImage:`url(${hoja})`, backgroundRepeat:'no-repeat', backgroundSize:'cover'};
const avatarMenu      = {width:'12rem', backgroundColor:'rgb(240, 179, 35)', marginBottom:'20px'};
const contTexto       = {display:'flex', justifyContent:'center'};
const contDimen       = {width:'90%', paddingBottom:'30px'};

class Subject4 extends Component {
  outMenu = (outState) => {
    this.props.outMenu(false);
  }
  render(){
    return (
      <div className="app-subject" style={pagina} onMouseOver={this.outMenu}>
        <div style={colSocial}>
          <div style={colDistribution}>
            <img className="circle" src="./img/DavidVanegas.png" alt="David Fernando Vanegas Marentes" style={avatarMenu}/>
          </div>
          <div>
            <ul>
            
              {/*<li>
                <div style={colSocialList}>
                  <img src={linkedin} alt="linkedin" style={iconSocial}/>
                  <a href="https://co.linkedin.com/in/jessica-marquez-zuluaga-a12b9b1a" target="_blank" rel="noreferrer" style={linkSocial}>Oscar Freddy Miranda Quiñones</a>
                </div>
              </li>*/}
              {/*<li>
                <div style={colSocialList}>
                  <img src={instagram} className="icon-instagram" alt="instagram" style={iconSocial}/>
                  <a href="https://www.instagram.com/andresfvalencia300/" target="_blank" rel="noreferrer" style={linkSocial}>andresfvalencia300</a>
                </div>
              </li>*/}
              {/*<li>
                <div style={colSocialList}>
                  <img src={facebook} className="circle" alt="facebook" style={iconSocial}/>
                  <a href="https://www.facebook.com/andresfelipe.valenciaferro" target="_blank" rel="noreferrer" style={linkSocial}>andres felipe valencia ferro</a>
                </div>
              </li>*/}
            </ul>
          </div>
        </div>
        <div style={colTexto}>
          <div>
            <h2>David Fernando Vanegas Marentes</h2>
            <h4>Desarrollador web</h4>
          </div>
          <div style={contTexto}>
            <div style={contDimen}>
            <p>
            David Vanegas es un desarrollador web, especializado en HTML,CSS, JAVASCRIPT y manejo del framework react
            </p>
            <h5>Competencias</h5>
            <ul>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> base de datos SQL.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/>Desarrollo web responsive</li>
             
            </ul>
            <h5>Habilidades</h5>
            <ul>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/>Manejo de base de datos SQL</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/>Manejo del framework REACT JS.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/>Manejo de codigo HTML,CSS,JS</li>
       
            </ul>
            <h5>Recorrido profesional</h5>
            <p>
              Su desarrollo profecional comprende su paso y contribución en tecnologias de TI y empresas de desarrollo de waresoft, nombradas a continuación:
            </p>
            <br></br>
            <ul>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/>Micorsoft.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/>Ubisoft.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/>Koulutus.</li>
              
            </ul>
            <br></br>
            <p>
              Desempeñando funciones complemetarias en cada una tales como planificación de proyectos web y su implementacion para el rendimiento de estos.
            </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Subject4;
