/*import librerias*/
import React, { Component } from 'react';
/*import components*/
import ListaNosotros from './ListaNosotros';

class Nosotros extends Component {
  openSubject = (opensubj)=>{
    this.props.opsubject(opensubj)
  }
  render(){
    const posit = this.props.posit;
    return (
      <div id="menuDrop">
        <ul id="dropdownMenu" style={{left:posit.x, top:posit.y}}>
          <li>
            <ListaNosotros
              id="subject1"
              img="./img/gente-koulutus.png" alt="La Empresa"
              title="Koulutus" subTitle="¿Quienes somos?"
              opsubject= {this.openSubject}
            />
          </li>
          <li>
            <ListaNosotros
              id="subject2"
              img="./img/Andres-Valencia-Koulutus.png" alt="Andres F. Valencia Ferro"
              title="Andres F. Valencia Ferro" subTitle="Fundador & CEO"
              opsubject= {this.openSubject}
            />
          </li>
          <li>
            <ListaNosotros
              id="subject3"
              img="./img/Jessica-Marquez-Koulutus.png" alt="Jessica Marquez Zuluaga"
              title="Jessica Marquez Zuluaga" subTitle="Directora de Negocios"
              opsubject= {this.openSubject}
            />
          </li>
          <li>
            <ListaNosotros
              id="subject4"
              img="./img/DavidVanegas.png" alt="David Fernando Vanegas"
              title="David Fernando Vanegas" subTitle="Desarrollador web"
              opsubject= {this.openSubject}
            />
          </li>
          <li>
            <ListaNosotros
              id="subject6"
              img="./img/Miguel-Padilla-koulutus.png" alt="Miguel A. Padilla Obando"
              title="Miguel A. Padilla Obando" subTitle="Desarrollador full stack"
              opsubject= {this.openSubject}
            />
          </li>
          <li>
            <ListaNosotros
              id="subject5"
              img="./img/famele.png" alt="Daniela Rodriguez"
              title="Daniela Rodriguez" subTitle="Comunity Manager"
              opsubject= {this.openSubject}
            />
          </li>
        
        </ul>
      </div>
    );
  }
}

export default Nosotros;
