/*import librerias*/
import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { faUsers } from '@fortawesome/free-solid-svg-icons';
import { faLaptop } from '@fortawesome/free-solid-svg-icons';
import { faRobot } from '@fortawesome/free-solid-svg-icons';
import { faHandshake } from '@fortawesome/free-solid-svg-icons';
import { faBars } from '@fortawesome/free-solid-svg-icons';





/*import css*/
import '../css/header.css';
import M from  'materialize-css';
import'../App.css';
/*import svg*/
import logo from '../svg/logo-koulutus.svg';
import facebook from '../svg/facebook.svg';
import instagram from '../svg/instagram.svg';
import linkedin from '../svg/linkedin.svg';
import twitter from '../svg/twitter.svg';
import youtube from '../svg/youtube.svg';
/*import components*/
import Nosotros from './Nosotros';
import Asesorias from './Asesorias';
import Desarrollo from './Desarrollo';
import Header from './Header';
import Section from './Section';
import Subject1 from './Subject1';
import Subject2 from './Subject2';
import Subject3 from './Subject3';
import Subject4 from './Subject4';
import Subject5 from './Subject5';
import Subject6 from './Subject6';


class Nav extends Component {
  constructor(props){
    super(props);
    this.state={
      nosotrosVw:false,
      asesoriaVw:false,
      desarrollosVw:false,
      x:0,
      y:0,
      headerVw:true,
      sectionVw:true,
      subject1Vw:false,
      subject2Vw:false,
      subject3Vw:false,
      subject4Vw:false,
      subject5Vw:false,
      subject6Vw:false,
    
    };
  }
  vwNosotros(posit) {
    this.setState({
      nosotrosVw:true,
      asesoriaVw:false,
      desarrollosVw:false,
      x:posit.left,
      y:posit.bottom
    });
  }
  vwEducacion(posit) {
    this.setState({
      nosotrosVw:false,
      asesoriaVw:true,
      desarrollosVw:false,
      x:posit.left,
      y:posit.bottom
    });
  }
  vwDesarrollo(posit) {
    this.setState({
      nosotrosVw:false,
      asesoriaVw:false,
      desarrollosVw:true,
      x:posit.left,
      y:posit.bottom
    });
  }
  clsVwMenu() {
    this.setState({
      nosotrosVw:false,
      asesoriaVw:false,
      desarrollosVw:false,
    });
  }
  socialOpen(socialUrl){
    window.open(socialUrl)
  }
  componentDidMount(){
    let dropdowns = document.querySelectorAll('.dropdown-trigger')
    for (let i = 0; i < dropdowns.length; i++){
    M.Dropdown.init(dropdowns[i]);
    };
    let tooltip = document.querySelectorAll('.tooltipped')
    for (let i = 0; i < tooltip.length; i++){
      M.Tooltip.init(tooltip[i]);
    };
    {
      let elems = document.querySelectorAll('.parallax')
      M.Parallax.init(elems);
    };
    {
      let elems = document.querySelectorAll('.collapsible')
      M.Collapsible.init(elems);
    };
  }
  outMenu = (outState) => {
    this.setState({
      nosotrosVw: outState,
    })
  }
  opsubject = (opsubject) =>{
    this.clsVwMenu();
    if(opsubject === 'subject1'){
      this.setState({
        headerVw:false,
        sectionVw:false,
        subject1Vw:true,
        subject2Vw:false,
        subject3Vw:false,
        subject4Vw:false,
        subject5Vw:false,
        subject6Vw:false,
     
      })
    };
    if(opsubject === 'subject2'){
      this.setState({
        headerVw:false,
        sectionVw:false,
        subject1Vw:false,
        subject2Vw:true,
        subject3Vw:false,
        subject4Vw:false,
        subject5Vw:false,
        subject6Vw:false,
      
      })
    };
    if(opsubject === 'subject3'){
      this.setState({
        headerVw:false,
        sectionVw:false,
        subject1Vw:false,
        subject2Vw:false,
        subject3Vw:true,
        subject4Vw:false,
        subject5Vw:false,
        subject6Vw:false,
   
      })
    };
    if(opsubject === 'subject4'){
      this.setState({
        headerVw:false,
        sectionVw:false,
        subject1Vw:false,
        subject2Vw:false,
        subject3Vw:false,
        subject4Vw:true,
        subject5Vw:false,
        subject6Vw:false,
       
      })
    };
    if(opsubject === 'subject5'){
      this.setState({
        headerVw:false,
        sectionVw:false,
        subject1Vw:false,
        subject2Vw:false,
        subject3Vw:false,
        subject4Vw:false,
        subject5Vw:true,
        subject6Vw:false,

      })
    };
    if(opsubject === 'subject6'){
      this.setState({
        headerVw:false,
        sectionVw:false,
        subject1Vw:false,
        subject2Vw:false,
        subject3Vw:false,
        subject4Vw:false,
        subject5Vw:false,
        subject6Vw:true,
   
      })
    };

    
  }
  onInicio(){
    this.setState({
      headerVw:true,
      sectionVw:true,
      subject1Vw:false,
      subject2Vw:false,
      subject3Vw:false,
      subject4Vw:false,
      subject5Vw:false,
      subject6Vw:false,

    })
  }


  render(){
    return (
      <div>
        <header className="App-header">
          <div className="header-barra">
            {/* Barra-Social */}
            <div className="social-baner-header" onMouseOver={(e)=> this.clsVwMenu()}>
              <div className="link-sociales">
                <img src={youtube}    className="tooltipped socialStyles" data-tooltip="Youtube"   alt="youtube"   onClick={(e)=> this.socialOpen('https://www.youtube.com/')}/>
                <img src={twitter}    className="tooltipped socialStyles" data-tooltip="Twitter"   alt="twitter"   onClick={(e)=> this.socialOpen('https://twitter.com/')}/>
                <img src={linkedin}   className="tooltipped socialStyles" data-tooltip="Linkedin"  alt="linkedin"  onClick={(e)=> this.socialOpen('https://www.linkedin.com/')}/>
                <img src={instagram}  className="tooltipped socialStyles" data-tooltip="Instagram" alt="instagram" onClick={(e)=> this.socialOpen('https://www.instagram.com/koulutus_sas/?hl=es-la')}/>
                <img src={facebook}   className="tooltipped socialStyles" data-tooltip="Facebook"  alt="facebook"  onClick={(e)=> this.socialOpen('https://www.facebook.com/KoulutusEmpresarial')}/>
              </div>
              <div className="baner">
                <h6><span style={{fontFamily:'GoldplayMedium'}}>EL MEJOR NEGOCIO</span> <span style={{fontFamily:'GoldplayBold', color:'rgb(240, 179, 35)'}}>ES INVERTIR EN TU NEGOCIO</span></h6>
              </div>
            </div>
            {/* Menu Dropdown */}
            {this.state.nosotrosVw?
              <Nosotros
                posit={this.state}
                opsubject={this.opsubject}
              />
              :null
            }
            {this.state.asesoriaVw?
              <Asesorias
                posit={this.state}
              />
              :null
            }
            {this.state.desarrollosVw?
              <Desarrollo
                posit={this.state}
              />
              :null
            }
            
            <nav>
              <div className="nav-wrapper">
                <div className="App-logo">

                <div className='bar'>
                <FontAwesomeIcon icon={ faBars}/>  
                </div>

                  <a href="#!" className="brand-logo" onClick={(e)=> this.onInicio()}>
                
                     
                    <img src={logo} alt="Logo Koulutus"/>
                    
                    
                  </a>

                  
                </div>
              
                <ul className="right hide-on-med-and-down">
               
                  <li>
                    <a href="#!"
                   
                      
                      // onClick={(e)=> this.onInicio()}
                      >
                     <FontAwesomeIcon icon={ faHome } style={{padding:' 0px 8px 0px 0px', width:'1.5rem'}}/>  Incio
                    
                    </a>
                  </li>
                  <li>
                    <a id="nosotros"  href="#!"
                      onClick={(e) => this.vwNosotros(e.target.parentElement.getBoundingClientRect())}
                      onMouseOver={(e)=> this.vwNosotros(e.target.parentElement.getBoundingClientRect())}
                      > <FontAwesomeIcon icon={ faUsers } style={{padding:' 0px 8px 0px 0px', width:'1.5rem'}}/> Nosotros <span style={{fontSize:"0.9em", margin:'0px', padding:'0px'}}>▼</span>
                    </a>
                  </li>
                  <li>
                    <a id="educacion" href="#!"
                      onClick={(e) => this.vwEducacion(e.target.parentElement.getBoundingClientRect())}
                      onMouseOver={(e)=> this.vwEducacion(e.target.parentElement.getBoundingClientRect())}
                      ><FontAwesomeIcon icon={ faHandshake } style={{padding:' 0px 8px 0px 0px', width:'1.5rem'}}/>Social <span style={{fontSize:"0.9em", margin:'0px', padding:'0px'}}>▼</span>
                    </a>
                  </li>
                  <li>
                    <a id="alianzas"  href="#!"
                      onClick={(e)=> this.G_developers()}
                      > <FontAwesomeIcon icon={ faLaptop } style={{padding:' 0px 5px 0px 0px', width:'1.3rem'}}/>Generation Developers
                    </a>
                  </li>
                  <li>
                    <a id="alianzas"  href="#!"
                      onMouseOver={(e)=> this.clsVwMenu()}
                      > <FontAwesomeIcon icon={ faRobot} style={{padding:' 0px 8px 0px 0px', width:'1.5rem'}}/>Koulutus Robotics
                    </a>
                  </li>
                  
                  {/* <li>
                    <a id="contactos" href="#!"
                      onClick={(e) => this.vwContactos(e.target.parentElement.getBoundingClientRect())} 
                      onMouseOver={(e)=> this.clsVwMenu()}
                      >Contacto
                    </a>
                  </li> */}
                </ul>
                
              </div>
            </nav>
          </div>
          
          {this.state.headerVw?
            <Header
              outMenu={this.outMenu}
            />
            :null
          }
        </header>
        {this.state.sectionVw?
          <Section/>
          :null
        }
        {this.state.subject1Vw?
          <Subject1
            outMenu={this.outMenu}
          />
          :null
        }
        {this.state.subject2Vw?
          <Subject2
            outMenu={this.outMenu}
          />
          :null
        }
        {this.state.subject3Vw?
          <Subject3
            outMenu={this.outMenu}
          />
          :null
        }
        {this.state.subject4Vw?
          <Subject4
            outMenu={this.outMenu}
          />
          :null
        }
        {this.state.subject5Vw?
          <Subject5
            outMenu={this.outMenu}
          />
          :null
        }
        {this.state.subject6Vw?
          <Subject6
            outMenu={this.outMenu}
          />
          :null
        }
      </div>
    );
  }
}

export default Nav;
