import React, { Component } from 'react';
/*import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCheckCircle} from '@fortawesome/free-solid-svg-icons';*/

import email from   '../svg/email.svg';
import facebook from '../svg/facebook.svg';
import instagram from '../svg/instagram.svg';
import linkedin from '../svg/linkedin.svg';
import hoja from '../svg/page-1.svg';

const pagina          = {display:'flex'};
const colSocial       = {margin:'20px', width:'20%'};
const colDistribution = {display:'flex', alignItems:'center', justifyContent:'center'};
const colTexto        = {width:'80%', color:'rgb(240, 179, 35)', fontFamily:'GoldplayMedium', backgroundImage:`url(${hoja})`, backgroundRepeat:'no-repeat', backgroundSize:'cover'};
const avatarMenu      = {width:'12rem', backgroundColor:'rgb(240, 179, 35)', marginBottom:'20px'};
const colSocialList   = {display:'inline-flex', alignItems:'center'};
const iconSocial      = {width:'1.8rem', marginRight:'8px'};
const linkSocial      = {color:'#6335ccff'};
const contTexto       = {display:'flex', justifyContent:'center'};
const contDimen       = {width:'90%', paddingBottom:'30px'};

class Subject1 extends Component {
  outMenu = (outState) => {
    this.props.outMenu(false);
  }
  render(){
    return (
      <div className="app-subject" style={pagina} onMouseOver={this.outMenu}>
        <div style={colSocial}>
          <div style={colDistribution}>
            <img className="circle" src="./img/gente-koulutus.png" alt="Corporación Koulutus" style={avatarMenu}/>
          </div>
          <div>
            <ul>
              <li>
                <div style={colSocialList}>
                  <img src={email} alt="email" style={iconSocial}/>
                  <a href="mailto:ceo@koulutus.com.co" rel="noreferrer" style={linkSocial}>info@koulutus.com.co</a>
                </div>
              </li>
              <li>
                <div style={colSocialList}>
                  <img src={linkedin} alt="linkedin" style={iconSocial}/>
                  <a href="#!" target="_blank" rel="noreferrer" style={linkSocial}>Corporación Koulutus</a>
                </div>
              </li>
              <li>
                <div style={colSocialList}>
                  <img src={instagram} className="icon-instagram" alt="instagram" style={iconSocial}/>
                  <a href="https://www.instagram.com/koulutus_sas/?hl=es-la" target="_blank" rel="noreferrer" style={linkSocial}>Koulutus_sas</a>
                </div>
              </li>
              <li>
                <div style={colSocialList}>
                  <img src={facebook} className="circle" alt="facebook" style={iconSocial}/>
                  <a href="https://www.facebook.com/KoulutusEmpresarial" target="_blank" rel="noreferrer" style={linkSocial}>Koulutus</a>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div style={colTexto}>
          <div>
            <h2>Corporación Koulutus</h2>
            <h4>Entidad sin Ánimo de Lucro</h4>
          </div>
          <div style={contTexto}>
            <div style={contDimen}>
              <p>
                Koulutus es una entidad sin ánimo de lucro que crea soluciones a diferentes situaciones culturales, sociales y empresariales buscando transformar la sociedad mediante la educación y la tecnología como eje transversal.
              </p>
              <h5>Visión</h5>
              <p>

              </p>
              <h5>Misión</h5>
              <p>

              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Subject1;
