/*import librerias*/
import React, { Component } from 'react';

const drowSubMenu   = {display:'flex', alignItems:'center', flexDirection:'column', fontWeight:'bold', color:'#444e57', borderTop:'solid 0.2px rgb(128 128 128 / 20%)', padding:'15px', position:'relative'};
const collapHeader  = {display:'flex', alignItems:'center', width:'100%'};
const titulo        = {fontFamily:'GoldplayLight',color:'#6335CC'};
const subTitulo     = {color:'rgb(240, 179, 35)'};
const avatarMenu    = {width:'3.5em', marginRight:'10px'};
const overlayer     = {position:'absolute', top:'0px', left:'0px', width:'100%', height:'100%'};

class ListaNosotros extends Component {
  openSubject = (opensubj)=>{
    let attr = opensubj.value;
    this.props.opsubject(attr);
  }
  render(){
    return (
      <div style={drowSubMenu} onClick={(e)=>{this.openSubject(e.target.getAttributeNode('id'))}}>
        <div style= {collapHeader}>
          <img src= {this.props.img} className="circle" alt= {this.props.alt} style={avatarMenu}/>
          <div style={{display:'flex', flexDirection:'column'}}>
            <span style= {titulo}>{this.props.title}</span>
            <span style= {subTitulo}>{this.props.subTitle}</span>
          </div>

        </div>
        <div id={this.props.id} className="listmenu" style={overlayer}></div>
      </div>
    );
  }
}

export default ListaNosotros;
