import React, { Component } from 'react';
import M from  'materialize-css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

const drowSubMenu = {display:'flex', alignItems:'center', fontWeight:'bold', backgroundColor:'white', color:'#444e57'};
const collapBody = {backgroundColor:'#6638b6', color:'white', fontFamily:'MonteserratLight', fontSize:'0.95em'};
const collapHeader = {display:'flex', flexDirection:'column'};
const btnPlus = {textAlign:'center', color:'white', backgroundColor:'rgb(240, 179, 35)'};
const titulo = {fontFamily:'MonteserratMedium', fontSize:'1em', color:'#6335CC'};
const subTitulo = {color:'rgb(240, 179, 35)', fontFamily:'GoldplayLight', fontSize:'0.85em'};
const aligBtn = {textAlign:'right'};
const avatarMenu = {width:'3.9em', marginRight:'10px'};

class Desarrollo extends Component {

    componentDidMount(){
        {
            let elems = document.querySelectorAll('.collapsible')
            M.Collapsible.init(elems);
        };
    }

    render(){
        const posit = this.props.posit;
        return (
            <div id="menuDrop">                 
                <ul id="dropdownMenu" className="collapsible" style={{left:posit.x, top:posit.y}}>
                    <li>
                        <div className="collapsible-header waves-effect" style={drowSubMenu}>
                            <img src= {this.props.img1} className="circle" alt= {this.props.alt1} style={avatarMenu}/>
                            <div style={collapHeader}>
                                <span style={titulo}>{this.props.title}</span>
                                <span style={subTitulo}>{this.props.subTitle}</span>
                            </div>
                        </div>
                        <div className="collapsible-body" style={collapBody}>
                            <span>
                                <p>{this.props.commentary}</p>                                
                            </span>
                            <span style={aligBtn}>
                                <p><a className="btn-floating pulse waves-effect waves-light" href="#!" style={btnPlus}><FontAwesomeIcon icon={faPlus}/></a></p>
                            </span>
                        </div>
                    </li>
                    <li>
                        <div className="collapsible-header waves-effect" style={drowSubMenu}>
                            <img src={this.props.img2} className="circle" alt={this.props.alt2} style={avatarMenu}/>
                            <div style={collapHeader}>
                                <span style={titulo}>{this.props.title1}</span>
                                <span style={subTitulo}>{this.props.subTitle1}</span>
                            </div>
                        </div>
                        <div className="collapsible-body" style={collapBody}>
                            <span>
                                <p>{this.props.commentary1}</p> 
                            </span>
                            <span style={aligBtn}>
                                <p><a className="btn-floating pulse waves-effect waves-light" href="#!" style={btnPlus}><FontAwesomeIcon icon={faPlus}/></a></p>
                            </span> 
                        </div>
                    </li>
                    <li>
                        <div className="collapsible-header waves-effect" style={drowSubMenu}>
                            <img src={this.props.img3} className="circle blue-grey darken-3" alt={this.props.alt3} style={avatarMenu}/>
                            <div style={collapHeader}>
                                <span style={titulo}>{this.props.title2}</span>
                                <span style={subTitulo}>{this.props.subTitle2}</span>
                            </div>
                        </div>
                        <div className="collapsible-body" style={collapBody}>
                            <span>
                                <p>{this.props.commentary2}</p> 
                            </span>
                            <span style={aligBtn}>
                                <p><a className="btn-floating pulse waves-effect waves-light" href="#!" style={btnPlus}><FontAwesomeIcon icon={faPlus}/></a></p>
                            </span> 
                        </div>
                    </li>
                </ul>   
            </div>
        );
    }
}

export default Desarrollo;