import React, { Component } from 'react';
import '../css/footer.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook } from '@fortawesome/free-brands-svg-icons';
import { faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faLinkedin } from '@fortawesome/free-brands-svg-icons';



class Header extends Component {
    render(){
        return (
            <footer className="page-footer">
                <div className="container">
                    <div className="row">
                        <div className="col l6 s12">
                            <h5 className="white-text">Corp. Koulutus</h5>
                            <p className="grey-text text-lighten-4">El Mejor Negocio es Invertir en tu Negocio</p>
                        </div>
                        <div className="col l4 offset-l2 s12">
                            <h5 className="white-text">Nuestras redes</h5>
                            <ul>
                                <li><a className="grey-text text-lighten-3" href="https://www.facebook.com/KoulutusCorp"> <FontAwesomeIcon icon={ faFacebook } style={{padding:' 0px 8px 0px 0px', width:'1.5rem'}}/>Facebook</a></li>
                                <li><a className="grey-text text-lighten-3" href="#!"><FontAwesomeIcon icon={ faInstagram } style={{padding:' 0px 8px 0px 0px', width:'1.5rem'}}/>Instagram</a></li>
                                <li><a className="grey-text text-lighten-3" href="#!"><FontAwesomeIcon icon={ faLinkedin} style={{padding:' 0px 8px 0px 0px', width:'1.5rem'}}/>Linkedin</a></li>
                                
                            </ul>
                        </div>

                      
                    </div>
                </div>
                <div className="footer-copyright">
                    <div className="container">
                        © 2015-2022 Corp. Koulutus, All rights reserved.
                        <a className="white-text right" href="#!">Powered by Corp. Koulutus</a>
                    </div>
                </div>
            </footer>
        );
    }
}

export default Header;
