import React, { Component } from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCheckCircle} from '@fortawesome/free-solid-svg-icons';

import email from   '../svg/email.svg';
import facebook from '../svg/facebook.svg';
import instagram from '../svg/instagram.svg';
import linkedin from '../svg/linkedin.svg';
import hoja from '../svg/page-1.svg';

const pagina          = {display:'flex'};
const colSocial       = {margin:'20px', width:'20%'};
const colDistribution = {display:'flex', alignItems:'center', justifyContent:'center'};
const colTexto        = {width:'80%', color:'rgb(240, 179, 35)', fontFamily:'GoldplayMedium', backgroundImage:`url(${hoja})`, backgroundRepeat:'no-repeat', backgroundSize:'cover'};
const avatarMenu      = {width:'12rem', backgroundColor:'rgb(240, 179, 35)', marginBottom:'20px'};
const colSocialList   = {display:'inline-flex', alignItems:'center'};
const iconSocial      = {width:'1.8rem', marginRight:'8px'};
const linkSocial      = {color:'#6335ccff'};
const contTexto       = {display:'flex', justifyContent:'center'};
const contDimen       = {width:'90%', paddingBottom:'30px'};

class Subject2 extends Component {
  outMenu = (outState) => {
    this.props.outMenu(false);
  }
  render(){
    return (
      <div className="app-subject" style={pagina} onMouseOver={this.outMenu}>
        <div style={colSocial}>
          <div style={colDistribution}>
            <img className="circle" src="./img/Andres-Valencia-Koulutus.png" alt="Andres F. Valencia Ferro" style={avatarMenu}/>
          </div>
          <div>
            <ul>
              <li>
                <div style={colSocialList}>
                  <img src={email} alt="email" style={iconSocial}/>
                  <a href="mailto:ceo@koulutus.com.co" rel="noreferrer" style={linkSocial}>ceo@koulutus.com.co</a>
                </div>
              </li>
              <li>
                <div style={colSocialList}>
                  <img src={linkedin} alt="linkedin" style={iconSocial}/>
                  <a href="https://co.linkedin.com/in/andres-felipe-valencia-ferro-12b0063a/en?trk=people-guest_people_search-card" target="_blank" rel="noreferrer" style={linkSocial}>andres felipe valencia ferro</a>
                </div>
              </li>
              <li>
                <div style={colSocialList}>
                  <img src={instagram} className="icon-instagram" alt="instagram" style={iconSocial}/>
                  <a href="https://www.instagram.com/andresfvalencia300/" target="_blank" rel="noreferrer" style={linkSocial}>andresfvalencia300</a>
                </div>
              </li>
              <li>
                <div style={colSocialList}>
                  <img src={facebook} className="circle" alt="facebook" style={iconSocial}/>
                  <a href="https://www.facebook.com/andresfelipe.valenciaferro" target="_blank" rel="noreferrer" style={linkSocial}>andres felipe valencia ferro</a>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div style={colTexto}>
          <div>
            <h2>Andres F. Valencia Ferro</h2>
            <h4>Fundador y CEO</h4>
          </div>
          <div style={contTexto}>
            <div style={contDimen}>
            <p>
              Andres F. Valencia, ademas de ser nuestro Fundador y Chief Executive Officer, es Licenciado en Informática Educativa con estudios avanzados en Ingeniería de Sistemas y Telecomunicaciones.
              Su recorrido por el area educativa le permitio desarrollar las competencias necesarias para la formación de educadores y educandos, abriendose paso hacia la coordinación académica y
              desarrollo curricular de la region y la zona suroccidente del valle del cauca.
            </p>
            <h5>Competencias</h5>
            <ul>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Manejo de equipos.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Elavoración y Estructuración de proyectos.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Conferencista de liderazgo y oratoria.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Coach PNL.</li>
            </ul>
            <h5>Habilidades</h5>
            <ul>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Espíritu cooperador y liderazgo.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Altamente creativo e innovador.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Eficiencia y responsabilidad.</li>
              <li><FontAwesomeIcon icon ={faCheckCircle} className="awesomeicon"/> Afinidad en el campo lógico-matemático.</li>
            </ul>
            <h5>Recorrido profesional</h5>
            <p>
              Docente desde el año 2006 siendo encargado de llevar a cabo el proyecto institucional de las asignaturas de matemática y geometría. rol ejercido por 5 años.
              El cual llevo a pasar al cargo de director de proyectos administrativos en una prestigiosa institución del sur de cali  donde se logro articularla con una
              universidad del sector. Ademas crear la feria del emprendimiento, la cual potencializo el trabajo realizado en todos los niveles academicos.
            </p>
            <p>
              Dentro de la misma institución se logro el cargo de coordinador académico el cual potencializó el crecimiento de la carrera profesional llevando a impactar
              mas a los docentes y estudiantes con la gestión situación que permitió proyectarse mas en el ámbito académico y buscar la manera de llegar a mas docentes.
              Es por esta razón que en el año 2016 toma la decisión de retirarse de la educación regular y comenzar un camino mas independiente por lo cual entra a trabajar
              en un proyecto del ministerio de educación nacional llamado colegio 10 tic operado por la universidad EAFIT de Medellín, donde se obtiene como logro el trabajo0
              con 4 instituciones impactando docentes de diferentes áreas en el campo tecnológico, en paralelo comienza un trabajo con un proyecto de la gobernación del
              Valle del Cauca operado por la corporación Talentum llamado Modelo educativo flexible Escuela Integral donde se apoyaba el proceso de enseñanza a el joven
              extra-edad y adulto mayor este proyecto iniciando como coordinador operativo. en los siguientes 3 años como coordinador académico y continuando como supervisor
              de la zona suroccidente actualmente. Ademas en este tiempo se ha realizado apoyo en diferentes proyectos en  con duración de meses como lo es la recolección de
              insumos para la creación de la política publica en el consumo de sustancias psicoactivas (SPA) proyecto aprobado por la gobernación del Valle del Cauca, operado
              por la fundación Ave Fenix.
            </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Subject2;
